import {onUpdated,ref,onMounted,onUnmounted} from 'vue'

// 这里就是对于数据的内存地址进行重复使用


//定义文章
export let article = ref()

//代码高亮
export function Code_highlight(Prism) { 
  onUpdated(() => Prism.highlightAll()) 
}

//如果使用就代表用户需要查看文章
export function UserArticleOpen({axios,router}){
  //定义文章内容
  article.value = {Title:null, Text:null}
  setTimeout(()=>{
    let to = router.currentRoute.value
    if (to.name === 'Root' && to.params.type === 'articles') {
      get_Article(to.params.id,axios)
    }
  }) 
}

//发送网络请求并修改文章内容
async function get_Article(ArticleID,axios) {
  const resultValue = await axios.get(`/send/get_articles/${ArticleID}`)
  article.value = {
    Title:resultValue.title,
    Text:resultValue.body
  }
}
