import { useRouter, onBeforeRouteUpdate } from 'vue-router'
import { onMounted, reactive, inject } from 'vue'
import ComponentChange from "./代理文章打开"
export default function (OpenButton,components) {
  const router = useRouter()
  const axios = inject('$axios')
  let OpenButton_Element = null

  onBeforeRouteUpdate((to, from, next) => {
    find_true(to)
    next()
  })

  //用 true/false 控制应当展出哪一个
  const flag = reactive({
    UsePage:""
  })

  //对于文章使用代理解耦
  let OpenComponents ={
    Open(ComponentName,setting){
      let { ComponentFlower, UseProxy } = ComponentChange()
      let Component_Name = new ComponentFlower(ComponentName)
      //传入 axios 与 router
      UseProxy.reactive(Component_Name,setting)
    }
  }


////////////////////////////////////////
  let [Article,Search] = components

  function find_true(to) {
    let name = to.name
    let type = to.params.type
    new Promise( resolve => {
      const flag = {
        Root: {
          articles: () => resolve([Article,"Article"]),
          Search: () => resolve([Search,"Search"]),
        },
      }
      flag[name][type] ? flag[name][type]() : null
    }).then((value) => {
      let [Component, ComponentName] = value
      flag.UsePage = Component
      //使用代理模式通知打开文章, 目的在于消除多次检测导航栏数值, 将 router 与 axios 传入
      OpenComponents.Open(ComponentName,{router,axios})
      //打开框框
      OpenButton_Element.click()
    })
  }

  function ExitButton() {
    router.replace('/')
  }

  onMounted(() => {
    OpenButton_Element = OpenButton.value
  })


  return { ExitButton,flag }
}
