import {UserArticleOpen} from "../新闻页面组件/文章块弹出效果/获取当前应当去拿取的文章.js"
export default function(){

  class ComponentFlower{
    constructor(Name){
      this.ComponentName = Name
    }
  }

  let UseProxy = {
    reactive(ComponentName,setting){
      UseComponents.reactive({...ComponentName,...setting})
    }
  }

  //在这里填写路由要触发的组件, 会自动传入 router 与 axios
  let map = new Map([
    ['Article',UserArticleOpen],
    // Search 组件则不需要写入, 因为当时为切换导航栏就切换组件, 样式加载上去之后是用户输入触发请求

  ])




  let UseComponents = {
    reactive(dataObj){
      let {ComponentName,router,axios} = dataObj
      map.has(ComponentName) && map.get(ComponentName)({router,axios})
    }
  }

  return {ComponentFlower,UseProxy}
}